* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Montserrat, monospace;
  color: #fff;
  padding: 50px;
  width: 100%;
  background-color: #374954;
  text-align: center;
}

h1 {
  font-size: 100px;
  margin-bottom: 40px;
}

.container {
  margin: 60% auto;
}

button {
  display: inline;
  background-color: #6bbe92;
  border: 0;
  border-radius: 10px;
  box-shadow: 5px;
  margin: 10px;
  text-align: center;
  color: #fff;
  font-weight: bold;
  font-size: 50px;
}
